import React from "react"
import { graphql } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"
import {
  PageContainer,
  H1,
  GridContainer,
  Card,
  BlogLink,
  BlogImage,
} from "../styledComponents/blog"

const Blog = props => {
  const posts = props.data.allMediumPost.edges

  return (
    <Layout pathname={props.location.pathname} headerColour="#555b6e">
      <SEO title="Blog" />
      <PageContainer>
        <H1>Blog</H1>
        <GridContainer>
          {posts.map(post => (
            <Card key={post.node.id}>
              <BlogLink slug={post.node.uniqueSlug}>
                <BlogImage image={post.node.virtuals.previewImage.imageId} />
                <div style={{ height: "240px", width: "100%" }} />
                <p>{post.node.title}</p>
              </BlogLink>
            </Card>
          ))}
        </GridContainer>
      </PageContainer>
    </Layout>
  )
}

export default Blog

export const pageQuery = graphql`
  query {
    allMediumPost(sort: { fields: [createdAt], order: DESC }) {
      edges {
        node {
          id
          uniqueSlug
          title
          virtuals {
            previewImage {
              imageId
            }
          }
        }
      }
    }
  }
`
